@import "../../css-config/mixins.scss";

.footer{
    width: 100%;
    .footer-content{
        padding: 50px 20px;
        max-width: 1200px;
        margin:0 auto;
        display: flex;
        justify-content: center;
        flex-flow: wrap;
        gap : 20px;
        @include md {
            justify-content: space-between;
            padding:50px 17px;
        }
        .col{
            width: 100%;
            text-align: center;
            @include md{
                width: 300px;
                text-align: left;
            }
            @include lg{
                max-width: 300px;
                width: auto;
            }
            .title{
                margin-bottom: 20px;
                font-size: 14px;
            }
            .text{
                color:rgba(0,0,0,0.5);
                font-size: 14px;
            }
            .c-item{
                display: flex;
                margin-bottom: 10px;
                justify-content: center;
                cursor: pointer;
                @include md{
                    text-align: left;
                    justify-content: flex-start;
                }
                svg{
                    flex-shrink: 0;
                    font-size: 14px;
                    margin-right: 10px;
                    margin-top: 4px;
                    color : rgba(0,0,0,0.5);
                }
            }
            span{
                &.text{
                    display: block;
                    margin-bottom: 10px;
                    cursor: pointer;
                }
            }
        }
    }

    .bottom-bar{
        border-top : 1px solid rgba(0,0,0,0.1);
        .bottom-bar-content{
            padding:20px;
            display: flex;
            align-content:center ;
            flex-direction: column;
            text-align: center;
            gap:10px;
            @include md{
                padding:20px;
                height:60px;
                max-width: 1200px;
                margin: 0 auto;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

            }
            .text{
                font-size: 12px;
                color:rgba(0,0,0,0.5);
            }
        }
    }
}


